import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Styles from './ForgotPassword.module.css';
import op2miseLogo from '../../assets/icons/Op2mizev0.6-for-light-bg.svg';

import ForgotPasswordService from './ForgotPasswordService';
import FooterComponent from '../../shared/footer/footer.component';

function ForgotPasswordComponent() {
  const navigate = useNavigate();
  const [email, setEmailInput] = useState('');
  const [isPasswordInitiated, setIsPasswordInitiated] = useState(false);
  // const [isResetPasswordClicked, setIsResetPasswordClicked] = useState(false)
  // const [showErrorAlert, setShowErrorAlert] = useState(false)
  // const [errorAlertMessage, setErrorAlertMessage] = useState('')
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  const handleResetPassword = () => {
    ForgotPasswordService.forgotPassword({ email });
    setIsPasswordInitiated(true);
  };

  const redirectToLoginPageOnClick = () => {
    navigate('login');
  };

  const inputOnChange = (e) => {
    setEmailInput(e.target.value);

    if (
      // eslint-disable-next-line
      /^[\w%\+\-]+(\.[\w%\+\-]+)*@[\w%\+\-]+(\.[\w%\+\-]+)+$/.test(
        e.target.value,
      )
    ) setIsButtonDisabled(false);
    else setIsButtonDisabled(true);
  };

  const triggerHandleResetPasswordOnKeDown = (e) => {
    if (e.key === 'Enter') handleResetPassword();
  };

  return (
    <div className={Styles.body}>
      {isPasswordInitiated ? (
        <div className={Styles.passwordInitiatedPanel}>
          <div className="container">
            <div className="row py-3 text-center">
              <p className={Styles.title}>
                <img
                  className={`${Styles.skyLogo} mt-4 mb-2`}
                  src={op2miseLogo}
                  alt="op2miseLogo"
                />
                <br />
                <br />
                <span style={{ marginLeft: '-40px' }}>
                  Password reset initiated
                </span>
              </p>
              <p className={Styles.description}>
                If the email address is in our database, we will send you an
                email shortly with instructions on how to reset your password.
              </p>
              <button
                className={Styles.redirectToSignInPageButton}
                onClick={redirectToLoginPageOnClick}
                type="button"
              >
                Back to sign in
              </button>
            </div>
          </div>
        </div>
      ) : (
        <div className={Styles.panel}>
          <div className="container">
            <div className="row py-3 text-center">
              <p className={Styles.title}>
                <img
                  className={`${Styles.skyLogo} mt-4 mb-2`}
                  src={op2miseLogo}
                  alt="op2miseLogo"
                />
                <br />
                <br />
                Forgot Password?
              </p>
              <p className={Styles.description} style={{ marginLeft: '70px' }}>
                Enter the email address associated with your account and
                we&apos;ll send you the link for resetting your password.
              </p>
              <input
                type="text"
                className={Styles.emailInput}
                style={{ marginLeft: '70px' }}
                placeholder="Email"
                value={email}
                autoComplete="off"
                onChange={inputOnChange}
                onKeyDown={triggerHandleResetPasswordOnKeDown}
              />
              <button
                className={Styles.resetPasswordButton}
                onClick={handleResetPassword}
                disabled={isButtonDisabled}
                type="button"
              >
                Reset Password
              </button>
              <p
                className={Styles.redirectUrlText}
                onClick={redirectToLoginPageOnClick}
                onKeyDown={redirectToLoginPageOnClick}
                role="presentation"
                style={{
                  cursor: 'pointer',
                  marginBottom: '50px',
                }}
              >
                Back
              </p>
            </div>
          </div>
        </div>
      )}
      <FooterComponent />
    </div>
  );
}
export default ForgotPasswordComponent;
